#hintHolder{left:0;position:fixed;top:0;z-index:10000007}.hint{-webkit-backdrop-filter:blur(2px)!important;backdrop-filter:blur(2px)!important;background-color:hsla(0,0%,99%,.9)!important;border-radius:4px;box-shadow:0 3px 10px 1px rgba(0,0,0,.1);display:block;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:0;padding:0;position:absolute;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.hint p{align-self:center;font-size:.75rem}.hint .hint-info{display:inline-flex;max-width:218px;padding:16px;width:-webkit-max-content;width:max-content}.hint .hint-info .hint-name{border-radius:3px;color:#fff;display:flex;font-weight:500;padding:8px;white-space:nowrap}.hint .hint-info .hint-description{word-wrap:break-word;color:#555;font-size:.65rem;font-weight:400;-webkit-hyphens:auto;hyphens:auto;line-height:150%;padding:0 0 0 10px}.hint .hint-info .hint-no-available{color:#ccc;font-style:italic}.hint .hint-category{border-top:1px solid #eee;color:#ccc;font-size:.65rem;margin:0 auto 8px;padding-bottom:0;padding-top:9px;text-align:center;width:calc(100% - 32px)}.hint.up{-webkit-transform:translate(-50%,calc(-100% - 10px));transform:translate(-50%,calc(-100% - 10px))}.hint.down{-webkit-transform:translate(-50%,10px);transform:translate(-50%,10px)}.hint.right{-webkit-transform:translate(8px,-50%);transform:translate(8px,-50%)}.hint.left{-webkit-transform:translate(calc(-100% - 10px),-50%);transform:translate(calc(-100% - 10px),-50%)}